@import url(https://fonts.googleapis.com/css?family=Lato:400,600,700|Work+Sans:400,700,800&display=swap);
.nav-link {
  padding: 20px 15px !important;
  text-transform: uppercase !important;
  font-size: 14px !important;
  letter-spacing: 1px !important;
  font-weight: 700 !important;
  color: #000 !important;
}
.nav-up {
  top: -50px !important;
  transition: top 0.2s ease-out !important;
  position: -webkit-sticky !important;
  position: sticky !important;
}
.navbar.solid {
  background-color: rgba(255, 255, 255, 0.5) !important;
  box-shadow: 0 0 4px grey !important;
}
.header-navigation {
  height: 50px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;

  z-index: 999;
}
a:hover {
  color: #f44336 !important;
  text-decoration: none;
  outline: none;
}
.nav-down {
  transition: top 0.1s ease-in !important;
}

html {
  overflow-x: hidden;
}

body {
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.166667px;
  line-height: 1.7em;
  color: #818385;
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Work Sans", sans-serif;
  color: #000;
  font-weight: 600;
}

h1,
.h1 {
  font-size: 45px;
  text-transform: none;
}

h2,
.h2 {
  font-size: 42px;
  line-height: 52px;
}

h3,
.h3 {
  font-size: 24px;
  line-height: 34px;
}

h4,
.h4 {
  font-size: 20px;
  line-height: 30px;
}

h5,
.h5 {
  font-size: 16px;
  line-height: 26px;
}

h6,
.h6 {
  font-size: 14px;
}

.text-sm {
  font-size: 14px;
}

